.container {
  text-align: center;
  width: 80%;
  background-color: #a2d2ee;
  border-radius: 5px;
  margin: 20px auto;
  padding: 20px 10px;
  position: relative;
}

.novisible {
  display: none;
}

.todo-form {
  margin-bottom: 10px;
}

.todo-item {
  min-width: 50vw;
  background: silver;
  border: 1px solid gray;
  border-radius: 4px;
  margin: 5px 0;
  padding: 5px 10px;
}

.todo-add {
  color: #0e0e36;
  margin-bottom: 10px;
  font-size: 17px;
  font-weight: bold;
  display: block;
}

.todo-add__input {
  height: 40px;
  width: 50%;
  color: #0f991d;
  background-color: #ceecce;
  border: 2px solid green;
  border-radius: 4px;
  font-size: 18px;
}

.todo-add__button {
  height: 40px;
  color: #eef60b;
  background-color: rgba(39, 203, 10, .86);
  border: 2px solid rgba(56, 56, 181, .792);
  border-radius: 4px;
  padding: 0 12px;
  font-size: 17px;
  font-weight: 700;
}

.todo-filter__label {
  color: #0e0e36;
  font-weight: bold;
}

.todo-filter__label:not(:last-child) {
  margin-right: 10px;
}

.todo-filter__input {
  height: 30px;
  color: #757575;
  border: 2px solid green;
  border-radius: 4px;
}

.loading {
  color: #716e6e;
  margin: 5px;
}

.todo-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline-block;
}

.button-delete {
  cursor: pointer;
  width: 30px;
  height: 30px;
  color: #fff;
  background: #a18d84;
  border: none;
  border-radius: 50%;
  margin-left: 15px;
}

.todo-buttons {
  justify-content: right;
  align-items: center;
  display: flex;
}

.todo-open {
  cursor: pointer;
  border-color: #a2d2ee;
}

.todo__data {
  color: #b35d5d;
  color: #0f991d;
  margin-right: auto;
  font-family: Courier New, Courier, monospace;
  font-size: 14px;
  font-weight: bold;
}

.todo__text {
  cursor: pointer;
  font-family: Courier New, Courier, monospace;
  font-size: 18px;
  font-weight: bold;
}

.todo__modal {
  min-width: 50vw;
  background-color: #f3f77e;
  border-radius: 15px;
  padding: 10px 30px;
  font-family: Courier New, Courier, monospace;
}

.todo-modal-text-title {
  color: #0f991d;
  padding-top: 20px;
}

.clock {
  color: #f4f5f6;
  background: #3f4a54;
  border: 1px solid #ff4500;
  border-radius: 5px;
  margin: 0;
  padding: 10px;
  animation: myAnim .85s 1s alternate backwards;
  position: absolute;
  top: -15px;
  left: -100px;
  box-shadow: -10px 10px 9px #5b63a6;
}

@keyframes myAnim {
  0% {
    opacity: 0;
    animation-timing-function: ease-in;
    transform: translateY(-250px);
  }

  38% {
    opacity: 1;
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  55% {
    animation-timing-function: ease-in;
    transform: translateY(-65px);
  }

  72% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  81% {
    animation-timing-function: ease-in;
    transform: translateY(-28px);
  }

  90% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }

  95% {
    animation-timing-function: ease-in;
    transform: translateY(-8px);
  }

  100% {
    animation-timing-function: ease-out;
    transform: translateY(0);
  }
}

.basicLightbox {
  width: 100%;
  height: 100vh;
  opacity: .01;
  z-index: 1000;
  will-change: opacity;
  background: rgba(0, 0, 0, .8);
  justify-content: center;
  align-items: center;
  transition: opacity .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.basicLightbox--visible {
  opacity: 1;
}

.basicLightbox__placeholder {
  max-width: 100%;
  z-index: 1;
  will-change: transform;
  transition: transform .4s;
  transform: scale(.9);
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  max-width: 95%;
  max-height: 95%;
  margin: auto;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.basicLightbox__placeholder > iframe:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  pointer-events: auto;
}

.basicLightbox__placeholder > img:first-child:last-child, .basicLightbox__placeholder > video:first-child:last-child {
  width: auto;
  height: auto;
}

.basicLightbox--iframe .basicLightbox__placeholder, .basicLightbox--img .basicLightbox__placeholder, .basicLightbox--video .basicLightbox__placeholder {
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.basicLightbox--visible .basicLightbox__placeholder {
  transform: scale(1);
}

